.container {
  width: calc(100vw - 60px);
  max-height: 100svh;
  overflow: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10;
  padding: 16px;

  .itemBlock {
    position: relative;
    width: calc(25% - 15px);
    min-width: 320px;
    min-height: 280px;
    height: 17vw;
    border: 2px dotted #f7f7f7;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0 0 5px #1b1b1be7;
    background: #1b1b1b;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.9;
    z-index: 1;

    > a {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 50px;
      cursor: url(https://cdn.custom-cursor.com/db/7263/32/material-space-pointer.png),
        pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      font-weight: bold;
      text-decoration: none;
      font-size: 20px;
      background: #000;
      text-transform: uppercase;
      opacity: 0.8;

      &[data-local="true"] {
        pointer-events: none;
      }

      &:hover {
        opacity: 1;
        text-shadow: 0 0 10px #ffffff9d;
      }
    }

    img {
      width: 80%;
      height: 80%;
      max-width: 150px;
      max-height: 150px;
      object-fit: contain;
      border-radius: 20px;
      filter: drop-shadow(0 0 1px #fff);
      pointer-events: none;
    }

    &:hover {
      opacity: 1;
      transform: scale(1.02);
      box-shadow: 0 0 10px #1b1b1b;
    }
  }

  @media (max-width: 768px) {
    .itemBlock {
      width: 100%;
      height: 55vw;
      min-width: 100%;
      min-height: 55vw;
    }
  }
}

.info {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 35px;
  height: 35px;
  cursor: url(https://cdn.custom-cursor.com/db/7263/32/material-space-pointer.png),
    pointer;
  background: none;
  border: none;
  outline: none;

  svg {
    width: 100%;
    height: 100%;
    pointer-events: none;
    fill: #fff;
  }
}

.modal {
  width: 100vw;
  height: 100svh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background: #0000004d;
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;

  .container {
    position: relative;
    width: 95%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    background: #1b1b1b;
    border-radius: 12px;
    box-shadow: 0 0 10px #ffffff8c;
    padding: 24px;

    video {
      width: 100%;
      height: 330px;
      box-shadow: 0 0 3px #ffffff8c;
      border-radius: 12px;
    }

    h2 {
      margin-block: 20px;
      width: 100%;
      text-align: center;
      color: #00e6f6;
      font-size: 28px;
    }

    h3 {
      color: #c9c9c9;

      a {
        color: blueviolet;
        cursor: url(https://cdn.custom-cursor.com/db/7263/32/material-space-pointer.png),
          pointer;
      }

      b {
        color: #fff;
      }
    }

    button {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 35px;
      height: 35px;
      cursor: url(https://cdn.custom-cursor.com/db/7263/32/material-space-pointer.png),
        pointer;
      background: none;
      border: none;
      outline: none;
      color: #fff;
      font-size: 30px;
      font-family: cursive, sans-serif;
    }
  }
}
